import React, { Component } from 'react'
import NewsItem from './NewsItem'

export class news extends Component {
   
    constructor(){
        super();
        console.log("hELLO I aM A CONSTRUCTOR FROM NEWS COMPONENT ")
        this.state = {
            articles:this.articles,
            loading:false
       }
    }

    async componentDidMount(){
        let url ="https://newsapi.org/v2/top-headlines?country=in&category=business&apiKey=9561da83005649bb9d198deb8dd116c0";
        let data = await fetch(url);
        let parseddata = await data.json();
        console.log(parseddata);
        this.setState({articles:parseddata.articles})

    }
    render() {
        return (
            <div className="container my-3">
        <h2>NewsAp- Top Headlines </h2>
        <div className="row">
            {this.state.articles.map((element)=>{
                    return <div className="col-md-3" key={element.url}>
                    <NewsItem title={element.title.slice(0,45)} description={element.description.slice(0,88)} urltoima={element.urlToImage} newsurl={element.url} />
           
                    </div>
            })}

         
        
         
        </div>
                  
               

            </div>
        )
    }
}

export default news
